*/
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  font-family: "Spartan", sans-serif;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --main-background: hsl(222, 26%, 31%);
  --toggle-keypad-background: hsl(223, 31%, 20%);
  --screen-background: hsl(224, 36%, 15%);
  --key-erase-background: hsl(225, 21%, 49%);
  --key-erase-shadow: hsl(224, 28%, 35%);
  --key-equal: hsl(6, 63%, 50%);
  --key-equal-shadow: hsl(6, 70%, 34%);
  --key-num-background: hsl(30, 25%, 89%);
  --key-num-shadow: hsl(28, 16%, 65%);
  --text-color-dark: hsl(221, 14%, 31%);
  --text-nav-color: hsl(0, 0%, 100%);
  --text-color-white: hsl(0, 0%, 100%);
}

:root.light-mode {
  --main-background: hsl(0, 0%, 90%);
  --toggle-keypad-background: hsl(0, 5%, 81%);
  --screen-background: hsl(0, 0%, 93%);
  --key-erase-background: hsl(185, 42%, 37%);
  --key-erase-shadow: hsl(185, 58%, 25%);
  --key-equal: hsl(25, 98%, 40%);
  --key-equal-shadow: hsl(25, 99%, 27%);
  --key-num-background: hsl(45, 7%, 89%);
  --key-num-shadow: hsl(35, 11%, 61%);
  --text-color-dark: hsl(60, 10%, 19%);
  --text-nav-color: hsl(60, 10%, 19%);
  --text-color-white: hsl(60, 10%, 19%);
}

:root.purple-mode {
  --main-background: hsl(268, 75%, 9%);
  --toggle-keypad-background: hsl(268, 71%, 12%);
  --screen-background: hsl(268, 71%, 12%);
  --key-erase-background: hsl(281, 89%, 26%);
  --key-erase-shadow: hsl(285, 91%, 52%);
  --key-equal: hsl(176, 100%, 44%);
  --key-equal-shadow: hsl(177, 92%, 70%);
  --key-num-background: hsl(268, 47%, 21%);
  --key-num-shadow: hsl(290, 70%, 36%);
  --text-color-dark: hsl(52, 100%, 62%);
  --text-color-blue: hsl(198, 20%, 13%);
  --text-nav-color: hsl(52, 100%, 62%);
  --text-color-white: hsl(52, 100%, 62%);
}

.calculator {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3.2rem;
  font-weight: 700;
  background-color: var(--main-background);
}
.calculator__container {
  width: 37.5%;
  display: flex;
  flex-direction: column;
}
.calculator__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  color: var(--text-nav-color);
  margin-bottom: 3.2rem;
}
.calculator__heading {
  line-height: 3.6rem;
}
.calculator__display {
  width: 100%;
  margin-bottom: 2.4rem;
  min-height: 12.8rem;
  background-color: var(--screen-background);
  color: var(--text-color-white);
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  padding: 1rem 3.2rem 2.5rem;
  gap: 0.5rem;
}
.calculator__display-prev-operand {
  font-size: 1.8rem;
}
.calculator__display-curr-operand {
  font-size: 4.8rem;
  line-height: 5.4rem;
}
.calculator__buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 2.5rem;
  row-gap: 2.4rem;
  background-color: var(--toggle-keypad-background);
  padding: 3.2rem 3rem;
  border-radius: 1rem;
}
.calculator__buttons .btn {
  padding: 1rem 1.3rem 1.4rem 1.4rem;
  border: none;
  border-radius: 1rem;
  cursor: pointer;
  font-size: inherit;
  font-family: inherit;
  line-height: 3.6rem;
  background-color: var(--key-num-background);
  color: var(--text-color-dark);
  box-shadow: inset 0px -4px 0px var(--key-num-shadow);
}
.calculator__buttons .calculator__buttons--erase {
  background-color: var(--key-erase-background);
  color: var(--text-color-white);
  box-shadow: inset 0px -4px 0px var(--key-erase-shadow);
  font-size: 2rem;
  text-transform: uppercase;
}
.calculator__buttons .calculator__buttons--equal {
  background-color: var(--key-equal);
  box-shadow: inset 0px -4px 0px var(--key-equal-shadow);
  color: var(--text-color-white);
}
.calculator__span-1 {
  grid-column: 1/span 2;
}
.calculator__span-2 {
  grid-column: 3/span 2;
}

.calculator__theme {
  display: flex;
  align-items: center;
  gap: 2.1rem;
}
.calculator__theme-small {
  font-size: 1.2rem;
  line-height: 1.3rem;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  align-self: flex-end;
  margin-bottom: 0.5rem;
}
.calculator__switch-container {
  border: none;
}
.calculator__switch-container .labels {
  font-size: 1.2rem;
  display: flex;
  gap: 1.7rem;
  padding: 0.5rem 0.6rem;
}

.theme-toggle {
  width: 7.1rem;
  display: flex;
  background-color: var(--toggle-keypad-background);
  border-radius: 1.3rem;
  padding: 0.3rem;
}

.custom-radio-button {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  position: relative;
}
.custom-radio-button:not(:last-child) {
  margin-right: 0.3rem;
}

.checkmark {
  background-color: var(--key-equal);
  position: absolute;
  top: 0.2rem;
  left: 0.2rem;
}

.custom-radio-button input {
  display: none;
}

.custom-radio-button .checkmark {
  width: calc(100% - .4rem);
  height: calc(100% - .4rem);
  background-color: var(--key-equal);
  border-radius: 50%;
  display: inline-block;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.custom-radio-button input:checked + .checkmark {
  opacity: 1;
}